@charset "UTF-8";
@import "../fonts/stylesheet.css";
@import "../fonts/style.css";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul,
li {
  list-style: none;
}

a,
a:hover {
  text-decoration: none;
}

input,
button,
select,
textarea {
  outline: none;
  border: none;
}

body {
  background: #fff;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
}

button:active {
  box-shadow: none;
}

.title__wrapper {
  width: 928px;
  height: 56px;
  line-height: 56px;
  padding-left: 30px;
  border-radius: 6px;
  background: #2F6DCE;
  margin-bottom: 56px;
  position: relative;
  z-index: 1;
}
.title__wrapper:after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: #2F6DCE;
  border-radius: 6px;
  top: 0;
  left: 0;
  z-index: -1;
  transform-origin: bottom left;
  -ms-transform: skew(-30deg, 0deg);
  -webkit-transform: skew(-30deg, 0deg);
  transform: skew(345deg, 0deg);
}

.title_right {
  padding-right: 30px;
  margin-left: auto;
  text-transform: uppercase;
  text-align: right;
}
.title_right:after {
  transform: skew(13deg, 0deg);
}

.title {
  font-family: "Uni";
  font-weight: 900;
  font-size: 32px;
  color: #FFFFFF;
}

.about {
  display: flex;
